import { FC, ReactNode, useCallback, useMemo, useState } from 'react';
import classNames from 'classnames';
import { TippyProps } from '@tippyjs/react';

// Components
import Container, { ContainerProps } from '../Container';
import Icon from '../Icon';
import Popover from '../Popover';

// Hooks
import { useSafeMobileCheck } from '@dtci/triton/hooks';

// Styles
import './NavBarToggle.scss';

// Types
import { TestMetadata } from 'types';

const defaultTippyProps: TippyProps = {
  theme: 'light-border',
  interactiveBorder: 10,
  interactive: true,
  maxWidth: '100vw',
  placement: 'bottom',
};

export interface NavBarToggleProps {
  /** Text to show in the nav bar */
  text?: string;
  /** Can pass in your own menu item to use if not text */
  menuItem?: ReactNode;
  /** Show overlay on either hover (default) or click */
  showChildrenOn?: 'hover' | 'click';
  /** Show arrow next to text menu item or not */
  showArrow?: boolean;
  /** Show arrow above tippy or not */
  showTippyArrow?: boolean;
  /** Should overlay switch to expand menu or overlay in mobile */
  overlayOnMobile?: boolean;
  /** Pass in custom props to tippy */
  tippyProps?: TippyProps;
  /** Custom class to use for menu item wrapper */
  wrapperClassName?: string;
  /** Custom class to use for tooltip */
  className?: string;
  /** Container props to spread to mobile menu */
  containerProps?: Omit<ContainerProps, 'children'>;
  /** Flag to indicate the toggle is not top level in a Nav Bar */
  isNested?: boolean;
  /** TestMetadata ids to use in toggle */
  testMetadata?: {
    container?: TestMetadata;
    mobileMenu?: TestMetadata;
  };
}

const NavBarToggle: FC<NavBarToggleProps> = ({
  text = 'Toggle',
  menuItem,
  children,
  showChildrenOn = 'hover',
  showArrow = true,
  showTippyArrow = true,
  wrapperClassName,
  className,
  overlayOnMobile = false,
  testMetadata,
  tippyProps,
  isNested = false,
  containerProps = {
    fill: 'white',
    margin: 'centered',
  },
}) => {
  const [open, setOpen] = useState<boolean>(false);
  const isMobile = useSafeMobileCheck({ initialValue: false });

  const sbTippyProps = useMemo(
    () => ({
      visible: isMobile || showChildrenOn === 'click' ? open : false,
      trigger:
        isMobile || showChildrenOn === 'click' ? 'click' : 'mouseenter focus',
      arrow: showTippyArrow,
      offset: [0, 0] as [
        number,
        number,
      ] /** [0, 0] being seen to ts as number[] unless cast */,
      ...defaultTippyProps,
      ...tippyProps,
    }),
    [open, showChildrenOn, showTippyArrow, tippyProps, isMobile],
  );

  const togglableMenuItemClasses = useMemo(
    () =>
      classNames(
        'sb-navbar-toggle',
        {
          'sb-navbar-toggle__inline-menu': !overlayOnMobile,
          'sb-navbar-toggle__nested': isNested,
          'sb-navbar-toggle__has-menu-item': menuItem,
        },
        wrapperClassName,
      ),
    [overlayOnMobile, menuItem, isNested, wrapperClassName],
  );

  const tooltipClasses = useMemo(
    () =>
      classNames(
        'sb-navbar-toggle__tooltip',
        {
          'sb-hide-on-mobile': !overlayOnMobile,
        },
        className,
      ),
    [overlayOnMobile, className],
  );

  const handleMenuClick = useCallback((): void => {
    setOpen(!open);
  }, [open]);

  return (
    <>
      <Popover
        tippyProps={sbTippyProps}
        className={tooltipClasses}
        popoverContent={<span>{children}</span>}
      >
        <div
          className={togglableMenuItemClasses}
          onClick={handleMenuClick}
          {...testMetadata?.container}
        >
          {menuItem ?? text}
          {showArrow && (
            <Icon
              altText="Toggle"
              iconName="fa-caret-down"
              faPackage="fas"
              iconType="fa"
              size="smaller"
              title="Toggle"
            />
          )}
        </div>
      </Popover>

      {!overlayOnMobile && open && (
        <Container
          className={classNames(
            'sb-navbar-toggle__mobile',
            'sb-show-only-on-mobile',
          )}
          {...containerProps}
          {...testMetadata?.mobileMenu}
        >
          {children}
        </Container>
      )}
    </>
  );
};

export default NavBarToggle;
